import React from 'react';
import { Card } from 'react-bootstrap';

const Cards = ({
    image,
    name 
}) => {

    return <div>
        <Card style={{ width: '18rem' }}>
            <Card.Img variant="top" src={image} />
            <Card.Body>
                <Card.Title>{name}</Card.Title>
            </Card.Body>
        </Card>
    </div>;
};

export default Cards;
